import React from "react";
import Profile from "./ui/Profile";
import { DOCTOR_SCREENS } from "./utils/constants";
import SectionTitle from "./ui/SectionTitle";
import SearchBar from "./ui/SearchBar";
import { useState, useMemo } from "react";

const DPatients = ({ setScreen }) => {
  const [search, setSearch] = useState();

  const filteredPatients = useMemo(() => {
    const patients = [
      { id: 9, name: "Amrendra Bahubali", lastVisit: "04/12/2024" },
      { id: 7, name: "Gangadhar Shaktiman", lastVisit: "04/12/2024" },
      { id: 8, name: "John Snow", lastVisit: "04/12/2024" },
      { id: 10, name: "Professor", lastVisit: "04/12/2024" },
      { id: 2, name: "James Bond", lastVisit: "04/12/2024" },
      { id: 3, name: "Jane Doe", lastVisit: "04/12/2024" },
      { id: 4, name: "Clint Joe", lastVisit: "04/12/2024" },
      { id: 5, name: "Manny Pack", lastVisit: "04/12/2024" },
      { id: 6, name: "Johnny James", lastVisit: "04/12/2024" },
    ];

    if (search) {
      return patients.filter((patient) =>
        patient.name.toLowerCase().match(search.toLowerCase())
      );
    } else {
      return patients;
    }
  }, [search]);

  return (
    <div className="flex flex-col">
      <div className="flex flex-start items-center">
        <svg
          xmlns="http://www.w3.org/2000/svg"
          fill="none"
          viewBox="0 0 24 24"
          strokeWidth={1.5}
          stroke="currentColor"
          className="size-6 ml-5 text-neu-dark"
          onClick={() => setScreen(DOCTOR_SCREENS.HOME)}
        >
          <path
            strokeLinecap="round"
            strokeLinejoin="round"
            d="m2.25 12 8.954-8.955c.44-.439 1.152-.439 1.591 0L21.75 12M4.5 9.75v10.125c0 .621.504 1.125 1.125 1.125H9.75v-4.875c0-.621.504-1.125 1.125-1.125h2.25c.621 0 1.125.504 1.125 1.125V21h4.125c.621 0 1.125-.504 1.125-1.125V9.75M8.25 21h8.25"
          />
        </svg>
        <SectionTitle title="Patients" />
      </div>
      <SearchBar setSearch={setSearch} />
      <div className="grid grid-cols-1 gap-3">
        {filteredPatients.map((patient) => (
          <Profile
            key={patient.id}
            name={patient.name}
            lastVisit={patient.lastVisit}
            onClick={() => setScreen(DOCTOR_SCREENS.HUMAN)}
          />
        ))}
      </div>
    </div>
  );
};

export default DPatients;
