import React from "react";

const StatCard = ({data, label, className, onClick}) => {
  return (
    <div className={`flex flex-col items-center bg-neu-light p-3 m-3 rounded-lg shadow-md hover:bg-neu focus:bg-neu ${className}`} onClick={onClick}>
      <p className="font-semibold">{data}</p>
      <p className="text-xs">{label}</p>
    </div>
  );
};

export default StatCard;
