import "./App.css";
import Header from "./ui/Header";
import { useState, useMemo } from "react";
import DemoHome from "./DemoHome";
import Patient from "./Patient";
import Doctor from "./Doctor";
import { POV } from "./utils/constants";

function App() {
  const [pov, setPov] = useState(POV.NONE);

  const currentPov = useMemo(() => {
    if (pov === POV.DOCTOR) {
      return <Doctor/>;
    } else if (pov === POV.PATIENT) {
      return <Patient/>;
    } else {
      return <DemoHome setPov={setPov}/>;
    }
  }, [pov])

  return (
    <div className="bg-neu w-screen h-screen overflow-y-scroll overflow-x-hidden">
      <Header title="Prototype" setPov={setPov} />
      {currentPov}
    </div>
  );
}

export default App;
