import React from "react";

// Default sample image
const DEFAULT_DP = "https://via.placeholder.com/150";

const Appointments = ({ dp, name, lastVisit, onClick }) => {
  return (
    <div className="flex items-center p-4 shadow-sm bg-neu-light rounded-lg mx-10 hover:bg-neu focus:bg-neu" onClick={onClick}>
      <img
        src={dp || DEFAULT_DP}
        alt={`${name}`}
        className="w-16 h-16 rounded-full object-cover mr-4 border"
      />
      <div>
        <h2 className="text-l font-semibold text-pri-dark">{name}</h2>
        <p className="text-xs text-neutral-400">Date: {lastVisit} Time: {1400}</p>
      </div>
    </div>
  );
};

export default Appointments;
