import React from "react";
import StatCard from "./ui/StatCard";
import SectionTitle from "./ui/SectionTitle";
import Appointments from "./ui/Appointments";
import { DOCTOR_SCREENS } from "./utils/constants";

const DHome = ({ setScreen }) => {
  const patients = [
    { id: 1, name: "John Doe", lastVisit: "04/12/2024" },
    { id: 2, name: "James Bond", lastVisit: "04/12/2024" },
    { id: 3, name: "Jane Doe", lastVisit: "04/12/2024" },
    { id: 4, name: "Clint Joe", lastVisit: "04/12/2024" },
    { id: 5, name: "Manny Pack", lastVisit: "04/12/2024" },
    { id: 6, name: "Johnny James", lastVisit: "04/12/2024" },
  ];

  return (
    <div>
      <SectionTitle title="Personalized Statistics" />
      <div className="grid grid-cols-3">
        <StatCard
          data="83"
          label="Current Patients"
          onClick={() => setScreen(DOCTOR_SCREENS.HUMANS)}
        />
        {/* <StatCard data="92%" label="Adherence" className="text-pri" /> */}
        <StatCard data="2" label="Need Attention" className="text-dan" />
        <StatCard
          data="7/13"
          label="Today"
          onClick={() => setScreen(DOCTOR_SCREENS.CALENDAR)}
        />
        {/* <StatCard data="26" label="Messages" /> */}
        {/* <StatCard data="34" label="Tasks" /> */}
      </div>
      <div className="flex items-center justify-center w-100 m-3 flex-col">
        <div className="grid grid-cols-5">
            <div className="w-[16vw] h-[3px] bg-neu-light"></div>
            <div className="w-[16vw] h-[3px] bg-sec-dark"></div>
            <div className="w-[16vw] h-[3px] bg-sec-dark"></div>
            <div className="w-[16vw] h-[3px] bg-neu-light"></div>
            <div className="w-[16vw] h-[3px] bg-sec-dark"></div>
        </div>
        <div className="flex justify-between w-[90vw] text-sm">
          <p>10:30</p>
          <p>15:30</p>
          <p>20:00</p>
        </div>
      </div>
      <SectionTitle title="Today's Upcoming Appointments (6)" />
      <div className="grid grid-cols-1 gap-3">
        {patients.map((patient) => (
          <Appointments
            key={patient.id}
            name={patient.name}
            lastVisit={patient.lastVisit}
            onClick={() => setScreen(DOCTOR_SCREENS.HUMAN)}
          />
        ))}
      </div>
    </div>
  );
};

export default DHome;
