import React from 'react';

const Card = ({title, icon, className, onClick}) => {
    return (
        <div className={`flex flex-col items-center p-4 border rounded-lg shadow-lg bg-neu-light w-[125px] h-[125px] m-5 hover:bg-neu focus:bg-neu ${className}`} onClick={onClick}>
            <div className="p-2">{icon}</div>
            <div className="p-2 font-semibold">{title}</div>
        </div>
    )
}

export default Card;