import React from "react";

const SearchBar = ({setSearch}) => {
  return (
    <div className="flex items-center justify-center mb-3">
      <input type="text" placeholder="Search" className="p-3 m-2 rounded-md bg-neu-light w-[90%]" onChange={(e)=>setSearch(e.target.value)}/>
    </div>
  );
};

export default SearchBar;
