import React from "react";
import StatCard from "./ui/StatCard";
import SectionTitle from "./ui/SectionTitle";
import { DOCTOR_SCREENS } from "./utils/constants";
import Divider from "./ui/Divider";
import EditableText from "./ui/EditableText";

const DEFAULT_DP = "https://via.placeholder.com/400";
const DPatient = ({ setScreen }) => {
  return (
    <div>
      <div className="flex flex-col items-center p-4 shadow-sm bg-neu-light rounded-lg m-10">
        <img
          src={DEFAULT_DP}
          alt="John Doe"
          className="w-32 h-32 rounded-full object-cover m-4 border"
        />
        <div className="flex flex-col items-center p-4 m-4">
          <h2 className="text-l font-semibold text-pri-dark">John Doe</h2>
          <p className="text-xs text-neutral-400">Last visited: 04/12/2024</p>
        </div>
      </div>

      <div className="flex flex-start flex-row items-center">
        <svg
          xmlns="http://www.w3.org/2000/svg"
          fill="none"
          viewBox="0 0 24 24"
          strokeWidth={1.5}
          stroke="currentColor"
          className="size-6 ml-10 text-neu-dark"
          onClick={() => setScreen(DOCTOR_SCREENS.HOME)}
        >
          <path
            strokeLinecap="round"
            strokeLinejoin="round"
            d="m2.25 12 8.954-8.955c.44-.439 1.152-.439 1.591 0L21.75 12M4.5 9.75v10.125c0 .621.504 1.125 1.125 1.125H9.75v-4.875c0-.621.504-1.125 1.125-1.125h2.25c.621 0 1.125.504 1.125 1.125V21h4.125c.621 0 1.125-.504 1.125-1.125V9.75M8.25 21h8.25"
          />
        </svg>
        <SectionTitle title="Patient Statistics" />
      </div>
      <div className="grid grid-cols-3">
        <StatCard data="54" label="Days" />
        <StatCard data="97%" label="Adherence" className="text-pri" />
        <StatCard data="1" label="Issues" className="text-dan" />
        {/* <StatCard data="7" label="Appointments" />
        <StatCard data="2" label="Messages" />
        <StatCard data="3" label="Tasks" /> */}
      </div>
      {/* <Divider /> */}
      <SectionTitle title="History" />
      <EditableText text="Patient presented with acute lower back pain following a recent fall. Pain is exacerbated by movement and coughing" />
      <SectionTitle title="Assessment" />
      <EditableText text="Physical examination revealed tenderness to palpation in the lumbar spine, decreased range of motion, and positive straight leg raise test." />
      <SectionTitle title="Treatment Regime" />
      <SectionTitle title="Exercise Regime" />
    </div>
  );
};

export default DPatient;
