import React from "react";
import { useState, useMemo } from "react";
import { DOCTOR_SCREENS } from "./utils/constants";
import DHome from "./DHome";
import DPatients from "./DPatients";
import DPatient from "./DPatient";
import DCalendar from "./DCalendar";
import DLogin from "./DLogin";

const Doctor = () => {
  const [dScreen, setDScreen] = useState(DOCTOR_SCREENS.HOME);

  const currentScreen = useMemo(() => {
    if (dScreen === DOCTOR_SCREENS.HUMANS) {
      return <DPatients setScreen={setDScreen} />;
    } else if (dScreen === DOCTOR_SCREENS.HUMAN) {
      return <DPatient setScreen={setDScreen} />;
    } else if (dScreen === DOCTOR_SCREENS.CALENDAR) {
      return <DCalendar setScreen={setDScreen} />;
    } else if (dScreen === DOCTOR_SCREENS.LOGIN) {
      return <DLogin setScreen={setDScreen} />;
    } else {
      return <DHome setScreen={setDScreen} />;
    }
  },[dScreen]);

  return currentScreen;
};

export default Doctor;
